<template>
  <div class="example">
    <v-contextmenu ref="contextmenu1" :theme="theme" @contextmenu="handleContextmenu">
      <v-contextmenu-item>1</v-contextmenu-item>
      <v-contextmenu-item>2</v-contextmenu-item>
      <v-contextmenu-item>3</v-contextmenu-item>
    </v-contextmenu>

    <v-contextmenu ref="contextmenu2" :theme="theme" @contextmenu="handleContextmenu">
      <v-contextmenu-item>2-1</v-contextmenu-item>
      <v-contextmenu-item>2-2</v-contextmenu-item>
      <v-contextmenu-item>2-3</v-contextmenu-item>
      <v-contextmenu-item>2-4</v-contextmenu-item>
    </v-contextmenu>

    <div :class="['box', theme]" v-contextmenu:contextmenu1>
      ContextMenu1
    </div>

    <div :class="['box', theme]" v-contextmenu:contextmenu1>
      ContextMenu1
    </div>

    <div :class="['box', theme]" v-contextmenu:contextmenu2>
      ContextMenu2
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Multiple',

    props: {
      theme: String,
    },

    methods: {
      handleContextmenu (vnode) {
        console.log(' vnode:', vnode)
      },
    },
  }
</script>

<style scoped>
  .box {
    width: 100%;
  }
</style>



